import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { PiCheckSquareDuotone } from "react-icons/pi";

interface ServiceData {
  title: string;
  content: { copy: string }[];
}

const ServicesCard: React.FC = () => {
  const { t } = useTranslation();

  const services: { key: string; data: ServiceData }[] = [
    {
      key: "Commercial",
      data: t("services.Commercial", { returnObjects: true }),
    },
    { key: "clinics", data: t("services.clinics", { returnObjects: true }) },
    {
      key: "pharmacies",
      data: t("services.pharmacies", { returnObjects: true }),
    },
    {
      key: "ourClients",
      data: t("services.ourClients", { returnObjects: true }),
    },
  ];

  return (
    <div className={styles.container}>
      {services.map((service) => (
        <div key={service.key} className={styles.card}>
          <h2 className={styles.title}>{service.data.title}</h2>
          <ol className={styles.list}>
            {service.data.content.map(
              (item: { copy: string }, index: number) => (
                <li key={index} className={styles.item}>
                    <div className="w-8 h-8 flex items-center">
                        <PiCheckSquareDuotone className="text-orange" />
                    </div>
                  <span className="text-left">{item.copy}</span>
                </li>
              )
            )}
          </ol>
        </div>
      ))}
    </div>
  );
};

export default ServicesCard;

const styles = {
  container: "max-w-screen-md mx-auto my-8",
  card: "w-full sm:w-[350px] bg-white rounded-[1rem] inline-block m-0 sm:m-4 my-4 p-4 shadow",
  title: "text-xl",
  list: "bg-lightGray/30 rounded-[1rem] p-4",
  item: "flex items-center leading-none my-2",
};