import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import images from "../images";

type ImageKeys = "integrity" | "quality" | "satisfaction" | "responsability" | "reliability";

interface ValueData {
  title: string;
  copy: string;
  image: ImageKeys;
}

const Values: React.FC = () => {
  const { t } = useTranslation();
  const values: ValueData[] = t("values", { returnObjects: true });

  return (
    <div className={styles.container}>
      {values.map((value, index) => {
        const image = images[value.image];
        return (
          <div key={index} className={styles.card}>

            <div className=" items-center">
                <h2 className={styles.title}>{value.title}</h2>
                <div className={styles.item}>
                <span className="text-left text-xs sm:text-base">{value.copy}</span>
                </div>

            </div>

            {image && (
              <img src={image.src} alt={image.alt} className={styles.image} />
            )}


          </div>
        );
      })}
    </div>
  );
};

export default Values;

const styles = {
  container: "max-w-screen-md mx-auto flex flex-wrap justify-center gap-4",
  card: "flex flex-row w-full bg-lightGray/50 rounded-[1rem] inline-block m-1 p-4 shadow justify-between",
  title: "text-base sm:text-xl font-semibold my-2",
  item: "flex items-start leading-none my-2",
  image: "w-24 h-auto rounded-md",
};
